#homebg {
    position: absolute;
    z-index: 0;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);   
    min-width: 100%;
    min-height: 100%;
    object-fit: cover;
}

.container {
    position: relative;
    right: 0;
    bottom: 0;
    min-width: 100%;
    min-height: 100%;
}

.overlay {
    position: absolute;
    top: 30%; 
    left: 20%;
    z-index: 1;
}

#welcome {
    color: green;
    font-size: 20px;
    align-self: center;
}